import { Capacitor } from '@capacitor/core'
import { Filesystem } from '@capacitor/filesystem'
import imageCompression from 'browser-image-compression'
import { observer } from 'mobx-react-lite'
import React, { useState } from 'react'
import * as FIcon from 'react-feather'
import { TiCameraOutline } from 'react-icons/ti'
import { RouteComponentProps } from 'react-router'
import { AdminItemDetail, AdminItemFieldRegistro, PageApp, PhotoLoaderModalRegistroMultiple } from '../../components'
import { SubHeader } from '../../components/SubHeader'
import { RUTAS_APP } from '../../constants'
import { comprimirImagen, useApiToaster, useToaster, useTryGoBack } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { Vehiculo } from '../../services/api'
import './VehiculoTransportista.css'

const moment = require('moment-timezone')
const numeral = require('numeral')

interface VehiculoTransportistaProps extends RouteComponentProps {}

const defaultValues = {
  habilitacion_frente: '',
  habilitacion_dorso: '',
  poliza_seguro: '',
  habilitacion_vencimiento: '',
}

interface previews {
  habilitacion_frente: string | null
  habilitacion_dorso: string | null
  poliza_seguro: string | null
}

const defaultPreviews = {
  habilitacion_frente: null,
  habilitacion_dorso: null,
  poliza_seguro: null,
}

export const VehiculoTransportista: React.FC<VehiculoTransportistaProps> = observer(({ history }) => {
  const rootStore = useStores()
  const { transportistaStore } = rootStore
  const [Loading, setLoading] = useState(false)
  const [NombreVehiculo, setNombreVehiculo] = useState('')
  const [Vehiculo, setVehiculo] = useState<Vehiculo>()
  const [Errors, setErrors] = useState(defaultValues)
  const [Datos, setDatos] = useState(defaultValues)
  const [Previews, setPreviews] = useState<previews>(defaultPreviews)
  const apiToast = useApiToaster()
  const addToast = useToaster()
  const tryGoBack = useTryGoBack()

  const setup = () => {
    if (transportistaStore.vehiculoDetalle) {
      const datos = { ...Datos }
      datos['habilitacion_vencimiento'] = moment(transportistaStore.vehiculoDetalle?.habilitacion_vencimiento).format(
        'DD/MM/YYYY',
      )
      setDatos(datos)
      setNombreVehiculo(
        transportistaStore.vehiculoDetalle.marca +
          ' ' +
          transportistaStore.vehiculoDetalle.modelo +
          ' ' +
          transportistaStore.vehiculoDetalle.numero_chapa,
      )
      // @ts-ignore
      setVehiculo(transportistaStore.vehiculoDetalle)

      setPreviews({
        habilitacion_dorso: transportistaStore.vehiculoDetalle.habilitacion_dorso,
        habilitacion_frente: transportistaStore.vehiculoDetalle.habilitacion_frente,
        poliza_seguro: transportistaStore.vehiculoDetalle.poliza_seguro,
      })
    }
  }

  React.useEffect(() => {
    setup()
  }, [transportistaStore.vehiculoDetalle])



  const activarVehiculo = (idVehiculo: number) => {
    setLoading(true)
    // @ts-ignore
    rootStore.environment.api.activarVehiculo(idVehiculo).then((result) => {
      setLoading(false)
      apiToast(result, 'Vehículo activado', 'al activar')
      if (result.kind === 'ok') {
        history.replace(RUTAS_APP.HOME)
      }
    })
  }

  const eliminarVehiculo = (idVehiculo: number) => {
    setLoading(true)
    rootStore.environment.api.eliminarVehiculo(idVehiculo).then((result: any) => {
      setLoading(false)
      apiToast(result, 'Eliminado satisfactoriamente', 'al eliminar')
      if (result.kind === 'ok') {
        history.replace(RUTAS_APP.VEHICULOS)
      } else if (result.kind === 'bad-data') {
        // @ts-ignore
        if (result.errors) {
          // @ts-ignore
          alert(result.errors[0])
        }
      } else {
        alert('Ocurrió un error al eliminar vuelva a intentar en unos momentos')
      }
    })
  }

  const setDato = (campo: string, valor: any) => {
    let datos = { ...Datos }
    // @ts-ignore
    datos[campo] = valor
    setDatos(datos)
  }

  const setPreview = (campo: string, valor: string) => {
    const previews = { ...Previews }
    //@ts-ignore
    previews[campo] = valor
    setPreviews(previews)
  }

  const imgSelected = async (data: any, tipo: string) => {
    if (Capacitor.getPlatform() !== 'web') {
      const contents = await Filesystem.readFile({
        path: data,
      })
      data = `data:image/jpeg;base64,${contents.data}`
    }
    let blob = await fetch(data).then((r) => r.blob())
    const imagen_comprimida = await comprimirImagen(blob, tipo, compromiendoImagen)
    const imagen_base64 = await imageCompression.getDataUrlFromFile(imagen_comprimida)
    // setInputComprimiendo(tipo)
    setDato(tipo, imagen_comprimida)
    setPreview(tipo, imagen_base64)
  }

  const compromiendoImagen = (porcentaje: number) => {}

  const guardar = () => {
    setLoading(true)
    setErrors(defaultValues)
    // @ts-ignore

    const datos: any = { ...Datos, transportista: transportistaStore.transportista.id }

    if (!!Vehiculo) {
      datos['id'] = Vehiculo.id
    }

    rootStore.environment.api.guardarVehiculo(datos, true).then((result: any) => {
      setLoading(false)
      if (result.kind === 'ok') {
        history.replace(RUTAS_APP.VEHICULOS)
      } else if (result.kind === 'bad-data') {
        addToast('Corrija los errores y vuelva a intentar', 'warning')

        setErrors(result.errors)

        if (result.errors.non_field_errors) {
          alert(result.errors.non_field_errors[0])
        }
      } else {
        alert('Ocurrió un error al guardar vuelva a intentar en unos momentos')
      }
    })
  }

  return (
    <PageApp
      titulo_header={NombreVehiculo}
      pageStyles={'transportista-page background-blanco detalle-vehiculo'}
      accion_boton_derecho={() => tryGoBack(RUTAS_APP.VEHICULOS)}
      boton_derecho={<FIcon.X size={30} color={'white'} />}
      sub_titulo={` `}
      mostrar_barra={false}
    >
     
      <SubHeader
        links={[
          { titulo: 'Perfil', ruta: RUTAS_APP.PERFIL },
          { titulo: 'Configuración', ruta: RUTAS_APP.CONFIGURACION },
          { titulo: 'Rodados', ruta: RUTAS_APP.VEHICULOS },
          { titulo: 'Detalles' },
        ]}
      />
      {!!Vehiculo && (
        <div id="detalle-vehiculo" className={'vehiculo-transportista-detail-container py-2'}>
          <h4 style={{ textAlign: 'center' }}>Detalles</h4>
          <div style={{ display: 'flex', justifyContent: 'center' }}>
            <img id="icono-auto" src={'assets/icon/car.png'} />
          </div>
          <div className="col-12">
            <strong>Tipo de Rodado:</strong> <span>{Vehiculo.tipo_vehiculo.nombre}</span>
          </div>
          <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr' }}>
            <AdminItemDetail
              titulo={'Marca:'}
              texto={Vehiculo?.marca}
              columnas={12}
              extra_class="campo-detalle-perfil"
            />
            <AdminItemDetail
              titulo={'Modelo:'}
              texto={Vehiculo?.modelo}
              extra_class="campo-detalle-perfil"
              columnas={12}
            />
            <AdminItemDetail titulo={'Año:'} texto={Vehiculo?.ano} columnas={12} extra_class="campo-detalle-perfil" />
            <AdminItemDetail
              titulo={'Color:'}
              texto={Vehiculo?.color}
              columnas={12}
              extra_class="campo-detalle-perfil"
            />
            <AdminItemDetail
              titulo={'Kilometraje:'}
              texto={Vehiculo?.kilometraje}
              columnas={12}
              extra_class="campo-detalle-perfil"
            />
            <AdminItemDetail
              titulo={'Chapa:'}
              texto={Vehiculo?.numero_chapa}
              columnas={12}
              extra_class="campo-detalle-perfil"
            />
          </div>

          <AdminItemDetail
            titulo={'Chasis:'}
            texto={Vehiculo?.numero_chassis}
            columnas={12}
            extra_class="campo-detalle-perfil"
          />
          <div className="col-12">
            <PhotoLoaderModalRegistroMultiple
              perfil={false}
              titulo_modal={'Póliza de seguro del rodado'}
              titulo_label={
                <>
                  <TiCameraOutline size={20} />
                  <span>Subir foto de la Póliza de seguro</span>
                </>
              }
              helptext={''}
              infoBody={'Subí una foto de la póliza de seguro al día.'}
              infoTitulo="Foto de Póliza de seguro"
              img_path={'assets/document.png'}
              on_select={imgSelected}
              on_accept={guardar}
              imageItems={[
                {
                  titulo: 'Poliza de seguro',
                  userDataLabel: 'poliza_seguro',
                  preview: Previews.poliza_seguro ? Previews.poliza_seguro : null,
                },
              ]}
              color={'#ff6f33'}
              class_boton_aceptar={'btn-outline-orange'}
            />
          </div>
          {Errors.poliza_seguro && <div className={'text-danger'}>{Errors.poliza_seguro}</div>}
          <div className="col-12">
            <PhotoLoaderModalRegistroMultiple
              titulo_label={
                <>
                  <TiCameraOutline size={20} />
                  <span>Subir foto de la Habilitación</span>
                </>
              }
              titulo_modal={'Subir foto de la habilitación'}
              helptext={''}
              infoBody={'Subí una foto del frente y dorso de tu habilitacion al día.'}
              infoTitulo="Foto de habilitación"
              modalInfoCssClass="info-modal-2lines"
              img_path={'assets/id.png'}
              on_select={imgSelected}
              on_accept={guardar}
              imageItems={[
                {
                  titulo: 'Habilitacion de rodado (frente)',
                  userDataLabel: 'habilitacion_frente',
                  preview: Previews.habilitacion_frente ? Previews.habilitacion_frente : null,
                },
                {
                  titulo: 'Habilitacion de rodado (dorso)',
                  userDataLabel: 'habilitacion_dorso',
                  preview: Previews.habilitacion_dorso ? Previews.habilitacion_dorso : null,
                },
              ]}
              perfil={false}
              color={'#ff6f33'}
              class_boton_aceptar={'btn-outline-orange'}
              extra_component={
                <>
                  <AdminItemFieldRegistro
                    titulo={'Fecha de vencimiento'}
                    titulo_externo={'Fecha de vencimiento'}
                    tipo={'fecha'}
                    campo={'habilitacion_vencimiento'}
                    valor={Datos ? Datos.habilitacion_vencimiento : ''}
                    set_dato={setDato}
                    columnas={12}
                    columnas_sm={6}
                    error={Errors && Errors.habilitacion_vencimiento}
                  />
                </>
              }
            />
          </div>
          {Errors.habilitacion_dorso && <div className={'text-danger mb-2'}>{Errors.habilitacion_dorso}</div>}
          {Errors.habilitacion_frente && <div className={'text-danger mb-2'}>{Errors.habilitacion_frente}</div>}
          {Errors.habilitacion_vencimiento && (
            <div className={'text-danger mb-2'}>{Errors.habilitacion_vencimiento}</div>
          )}
          <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr' }}>
            <AdminItemDetail
              titulo={'Fecha de vencimiento'}
              texto={Datos.habilitacion_vencimiento}
              columnas={12}
              extra_class="campo-detalle-perfil"
              info_icon={<img src="assets/icon/calendar.png" />}
            />
          </div>
        </div>
      )}
    </PageApp>
  )
})

export default VehiculoTransportista
